// base
html {
  scroll-behavior: smooth;
  }

body {
  font-family: 'Noto Sans JP', sans-serif;
  color: #4D5156;
}

h1 , h2 , p {
  margin: 0;
}

.fixed-btn {
  position: fixed;
  bottom: 40px;
  right: 20px;
  max-width: 100px;
  z-index: 1000;
  opacity: .9;
}

.fixed-btn img {
  width: 100%;
}

.headline {
  font-weight: bold;
  font-size: 20px;
  border-radius: 50px;
  color: #fff;
  max-width: 300px;
  margin-inline: auto;
  padding-block: 6px;
  text-align: center;
}

.wrapper {
  padding-inline: 4.2%;
  padding-block: 16px;
}

// fv
.fv__item{
  
  img {
    max-width: 100%;
  }
}

// about
.about {
  background-color: #FFF9B3;

  .headline {
    background-color: #E83328;
  }

  .about__description {
    font-size: 12px;
    margin-top: 16px;
  }

  .about__limit {
    font-size: 12px;
    color: #E83328;
    font-weight: bold;
    margin-top: 8px;
  }
}

// how_to_apply
.how_to_apply {

  .headline {
    background-color: #50a199;
  }

  .apply_flow {
    margin-top: 24px;
    img {
      max-width: 100%;
    }
  }

  .apply_flow__description {
    font-size: 9px;
    margin-bottom: 24px;
  }

  .apply__cont {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .check__cont {
      width: 26%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .check__img {
        width: 100%;

        img {
          width: 100%;
        }
      }

      .attention {
        width: 100%;
        font-size: 20px;
        font-weight: bold;
        text-align: center;
        padding: 11px 15px;
        background-color: #E83328;
        color: #fff;
      }
    }
    
    .check__descriptions {
      width: 70%;
      font-size: 9px;
    }
  }
}

// prize
.prize {
  background-color: #EFD4CD;

  .headline {
    background-color: #fff;
    color: #E83328;
    margin-bottom: 40px;
  }

  .prize__cont {
    display: flex;
    flex-direction: column;

    .prize__item {
      margin-bottom: 24px;
      img {
        max-width: 100%;
      }
    }
  }
}

// map
.map {

  .headline {
    background-color: #00B4D9;
    color: #fff;
  }

  .map__cont {
    margin-top: 40px;
    img {
      max-width: 100%;
    }
  }
}

// form
.form {
  background-color: #86BAE5;
  text-align: center;

  .headline {
    background-color: #fff;
    color: #86BAE5;
  }

  .form__description {
    color: #fff;
    font-size: 12px;
    margin-top: 16px;
  }
}

// footer
footer {
  background-color: #D9D9D9;
  color: #fff;
  text-align: center;
  font-size: 8px;
  padding-block: 8px;

}